html{
    font-size: 62.5%;
}

html.sr .load-hidden{
    visibility: hidden;
}

body{
    font-family: "Montserrat", sans-serif;
    text-align: center;
    scroll-behavior: smooth;
}

/* Scrollbar */
::-webkit-scrollbar{
    width: 10px;
    height: 10px;
}

::-webkit-scrollbar-track{
    background: #f1f1f1;
}

::-webkit-scrollbar-thumb{
    background: var(--light-gray);
}

::-webkit-scrollbar-thumb:hover{
    background: var(--primary-color);
}